					if (jsondata.status = 1) {
						console.log("检查区域获取成功");
						this.areaList = this.setData(jsondata.data);
						this.areaVal = this.areaList[this.areaIndex].value;
						this.areaText = this.areaList[this.areaIndex].text;
					} else {
